
        * {
            padding: 0;
            margin: 0;
        }
        #topHalf {

            top: 0;
            left: 0;
            min-width: 100%;
            height: 50vh;
            background: #eee;
            position: fixed;
            overflow-y: auto;
        }
        #topHalf::-webkit-scrollbar {
            display: none;
        }
        #bottomHalf {
            min-width: 100%;
            position: fixed;
            top: 50vh;
        }
        #btn {
            position: fixed;
            display: flex;
            flex-direction: column;
            top: 10px;
            right: 10px;
        }
        #btn>button {
            margin-bottom: 10px;
            padding:5px;
        }
